/* istanbul ignore next */
const getData = (params, callback) => {
  const { cacheName, cache, firstStep, steps } = params;
  const currentStep = firstStep;
  const renderedSteps = [steps[currentStep.id]];
  const previousSteps = [steps[currentStep.id]];
  const previousStep = {};

  let unParsedCache = window.rsc.getItem(cacheName);

  const doParse = unParsedCache => {
    if (cache && unParsedCache) {
      try {
        const data = window.rsc.parse(unParsedCache);
        const lastStep = data.renderedSteps[data.renderedSteps.length - 1];

        if (lastStep && lastStep.end) {
          window.rsc.removeItem(cacheName);
        } else {
          for (let i = 0, len = data.renderedSteps.length; i < len; i += 1) {
            const renderedStep = data.renderedSteps[i];
            // remove delay of cached rendered steps
            data.renderedSteps[i].delay = 0;
            // flag used to avoid call triggerNextStep in cached rendered steps
            data.renderedSteps[i].rendered = true;

            // an error is thrown when render a component from localStorage.
            // So it's necessary reassing the component
            if (renderedStep.component) {
              const { id } = renderedStep;
              data.renderedSteps[i].component = steps[id].component;
            }
          }

          const { trigger, end, options } = data.currentStep;
          const { id } = data.currentStep;

          if (options) {
            delete data.currentStep.rendered;
          }

          // add trigger function to current step
          if (!trigger && !end) {
            if (options) {
              for (let i = 0; i < options.length; i += 1) {
                data.currentStep.options[i].trigger = steps[id].options[i].trigger;
              }
            } else {
              data.currentStep.trigger = steps[id].trigger;
            }
          }

          // execute callback function to enable input if last step is
          // waiting user type
          if (data.currentStep.user) {
            callback();
          } else {
            //Fix for being stuck after quitting in the middle of step transition
            //To reproduce, refresh during step transitions
            const lastPreviousStep  = data.previousSteps[data.previousSteps.length - 1];
            const lastRenderedStep  = data.renderedSteps[data.renderedSteps.length - 1];
            const lpsMatch  = (lastPreviousStep.id === data.previousStep.id);
            const cpMatch   = (data.currentStep.id === data.previousStep.id);
            const lrlpMatch = (lastRenderedStep.id === lastPreviousStep.id);
            const clrMatch  = (data.currentStep.id === lastRenderedStep.id);

            if (!cpMatch && lrlpMatch && !clrMatch) {
              data.renderedSteps.push(data.currentStep);
              if ('component' in data.currentStep) {
                const c = steps[data.currentStep.id].component;
                data.renderedSteps[data.renderedSteps.length-1].component = c;
                data.renderedSteps[data.renderedSteps.length-1].delay = 1000;
              }
              data.renderedSteps[data.renderedSteps.length-1].rendered = false;
            } else if (!cpMatch && !lrlpMatch && !clrMatch && !lpsMatch) {
              data.previousSteps.push(data.previousStep);
              data.renderedSteps.push(data.currentStep);
              data.renderedSteps[data.renderedSteps.length - 1].rendered = false;
            }
          }

          return data;
        }
      } catch (error) {
        console.info(
          `Unable to parse cache named:${cacheName}. \nThe cache where probably created with an older version of react-simple-chatbot.\n`,
          error
        );
      }
    }

    return {
      currentStep,
      previousStep,
      previousSteps,
      renderedSteps
    };
  };

  if (unParsedCache && unParsedCache.then !== undefined) {
    return unParsedCache.then(unParsedCache => doParse(unParsedCache));
  }

  return Promise.resolve(doParse(unParsedCache));
};

/* istanbul ignore next */
const setData = (cacheName, cachedData) => {
  const data = window.rsc.parse(window.rsc.stringify(cachedData));
  // clean components
  for (const key in data) {
    for (let i = 0, len = data[key].length; i < len; i += 1) {
      if (data[key][i].component) {
        data[key][i].component = data[key][i].id;
      }
    }
  }

  window.rsc.setItem(cacheName, window.rsc.stringify(data));
};

export { getData, setData };
